import React from 'react'

class Letter extends React.Component {
    constructor() {
        super()
        this.state = {
            classname: ' letter hide',
            theposition: window.pageYOffset,
        }
        this.reveal = this
            .reveal
            .bind(this)
    }

    reveal() {
        const index = this.props.key
        setTimeout(() => {
            this.setState({classname: 'letter'})
        }, index * 10000);
    }

    componentDidMount() {
        window.addEventListener('load', this.reveal)
    }

    componentWillUnmount() {
        window.addEventListener('load', this.reveal)
    }

   
    render() {
        const {classname} = this.state
        return (
            <span className={classname}>{this.props.letter}</span>
        )
    }
}

class Intro extends React.Component {
    constructor() {
        super()
        this.state = {
            title: 'Christmas Gift guide'
        }
    }
    render() {

        const title = this
            .state
            .title
            .split('')

        console.log(title.length)
        return (
            <div className="col-25 intro justify-center align-center">
                <div className='col-23 justify-center align-center'>
                    <h1 className='title-intro align-center light'>{title.map((letter, index) => <Letter key={index} letter={letter}/>)}</h1>
                </div>
            </div>
        )
    }
}

export default Intro