import React from 'react';
import Navigation from './Navigation';

class Header extends React.Component {
    constructor() {
        super()

        this.el = React.createRef();

        this.fadeIn = this
            .fadeIn
            .bind(this)
        this.state = {
            link: 'For him'
        }
    }

    fadeIn() {
        const node = this.el.current;
        setTimeout(() => node.classList.remove('hide'), 800)
    }

    componentDidMount() {
        window.addEventListener('load', this.fadeIn)
    }

    render() {
        const {link} = this.state

        return ( <> <div className={`col-25 head ${this.props.bgcolor} align-end`}>
            <div
                className="col-14 col-s-25 video-wrapper"
                dangerouslySetInnerHTML={{
                __html: `<video class="video" autoplay loop muted playsinline> <source src=${this.props.video} alt="video" type="video/mp4"/></video>`
            }}/>
            <div
                ref={this.el}
                className="title-bloc offset-15 col-9 offset-s-1 col-s-23 flex-col hide">
                <p className="text-s light mgbt-text-30">Lacoste gift guide</p>
                <h1 className="big-title light">{this.props.title}</h1>
                <p className="text-s light">Lorem ipsum Nam Donec malesuada leo, vehicula
                    commodo Morbi tincidunt lacus aliquet ultrices tempor scelerisque neque Cras sed
                    vitae eget Morbi rhoncus feugiat ultricies.</p>
            </div>
        </div> < Navigation eventmen = {
            this.props.eventmen
        }
        eventwomen = {
            this.props.eventwomen
        }
        eventkids = {
            this.props.eventkids
        }
        hide1 = {
            this.props.hide1
        }
        hide2 = {
            this.props.hide2
        }
        hide3 = {
            this.props.hide3
        }
        bgcolor = {this.props.bgcolor}
        link = {
            link
        } /> </>)
    }
}

export default Header