import React from 'react'

class Title extends React.Component {
    constructor() {
        super()
        this.el = React.createRef();
        this.state = {
            width: 'offset-1 col-23',
            fontSize: '',
            marginBottom: ''
        }

        this.reveal = this
            .reveal
            .bind(this);

        this.setWidth = this
            .setWidth
            .bind(this);

        this.setFontSize = this
            .setFontSize
            .bind(this);

        this.setMarginBottom = this
            .setMarginBottom
            .bind(this);

    }

    setWidth() {
        if (this.props.width === 'full') {
            this.setState({width: 'offset-1 col-23'})
        }
        if (this.props.width === 'mid') {
            this.setState({width: 'offset-6 col-13'})
        }
    }

    setFontSize() {
        if (this.props.fontSize === 'small') {
            this.setState({fontSize: 'small-title'})
        }
        if (this.props.fontSize === 'medium') {
            this.setState({fontSize: 'medium-title'})
        }
        if (this.props.fontSize === 'big') {
            this.setState({fontSize: 'big-title'})
        }
    }

    setMarginBottom() {
        if (this.props.marginBottom === 'none') {
            this.setState({marginBottom: ''})
        }
        if (this.props.marginBottom === 'medium') {
            this.setState({marginBottom: 'mgbt-1 mgbt-m-2'})
        }
        if (this.props.marginBottom === 'big') {
            this.setState({marginBottom: 'mgbt-2 mgbt-m-4'})
        }
    }

    reveal() {
        const node = this.el.current;
        const rect = node.getBoundingClientRect();
        const dist = rect.top
        if (dist < window.innerHeight) {
            setTimeout(() => node.classList.remove('hide'), 500)
            setTimeout(() => node.classList.add('show'), 500)
        } else {
            setTimeout(() => node.classList.add('hide'), 500)
            setTimeout(() => node.classList.remove('show'), 500)
        }
    }

    componentDidMount() {
        window.addEventListener('scroll', this.reveal)
        window.addEventListener('load', this.setWidth)
        window.addEventListener('load', this.setFontSize)
        window.addEventListener('load', this.setMarginBottom)
    }

    componentWillUnmount() {
        window.removedEventListener('scroll', this.reveal)
        window.removeEventListener('load', this.setWidth)
        window.removeEventListener('load', this.setFontSize)
        window.removeEventListener('load', this.setMarginBottom)
    }

    render() {

        const {title, text} = this.props;
        return (
            <div ref={this.el} className={`col-25 mgt-2 mgt-m-4 ${this.state.marginBottom}`}>
                <div className={`${this.state.width} offset-s-1 col-s-23 flex-col align-start`}>
                    <h1 className={`${this.state.fontSize} text-custom dark mgbt-text-10`}>{title}</h1>
                    <p className="text-s dark">{text}</p>
                    {this.props.button}
                </div>
            </div>
        )
    }
}

export default Title