import React from 'react'
import bag1 from './images/bag1.jpg'
import bag2 from './images/bag2.jpg'
import giftmen1 from './images/giftmen1.jpg'
import giftmen2 from './images/giftmen2.jpg'

class Parallax extends React.Component {
    constructor() {
        super()
        this.state = {
            img2pos: 0
        }

        this.parallax = this
            .parallax
            .bind(this)

        this.reveal = this
            .reveal
            .bind(this)

        this.elt = React.createRef()
        this.elt2 = React.createRef()
        this.elt3 = React.createRef()
    }

    reveal() {
        const node = this.elt3.current;
        const dist = node
            .getBoundingClientRect()
            .top;
        if (dist < window.innerHeight) {
            setTimeout(() => node.classList.remove('hide-parallax'), 500)
            setTimeout(() => node.classList.add('show-parallax'), 500)
        } else {
            setTimeout(() => node.classList.add('hide-parallax'), 500)
            setTimeout(() => node.classList.remove('show-parallax'), 500)
        }
    }

    parallax() {
        const eltPosition = this
            .elt
            .current
            .getBoundingClientRect()
            .top - window.innerHeight;
        if (eltPosition < 0) {
            this.setState({
                img2pos: eltPosition / 6
            })
        } else {
            this.setState({img2pos: 0})
        }
    }
    componentDidMount() {
        window.addEventListener('scroll', this.parallax)
        window.addEventListener('scroll', this.reveal)
    }

    render() {
        const {img2pos} = this.state
        return (
            <div ref={this.elt3} onClick={this.props.event} className="col-25 mgbt-1 mgbt-m-2 relative">
                <div ref={this.elt2}
                    className='offset-3 col-12 offset-s-0 col-s-19'
                    >
                    <div className="row portrait">
                        <img className="tileimg" src={this.props.img1} alt='img'/>
                    </div>
                </div>
                <div
                    ref={this.elt}
                    className='offset-13 col-9 offset-s-12 col-s-12 mgt-5 mgt-m-10 upper absolute'
                    style={{
                    transform: `translateY(${img2pos}px)`
                }}>
                    <div className="row portrait">
                        <img className="tileimg" src={this.props.img2} alt='img'/>
                    </div>
                </div>
            </div>
        )
    }
}

export default Parallax