import React from 'react';
import Navigation from './Navigation';

class Header2 extends React.Component {
    constructor() {
        super()

        this.elt1 = React.createRef();
        this.elt2 = React.createRef();

        this.parallax = this
            .parallax
            .bind(this)
        this.state = {
            link: 'For him',
            text1pos: 0,
            text2pos: 600
        }
    }


    parallax() {
        const eltPosition = this
            .elt1
            .current
            .getBoundingClientRect()
            .top - window.innerHeight;

     
            this.setState({
                text1pos: window.scrollY / 3,
                text2pos: -window.scrollY / 3
            })
       
    }

    componentDidMount() {
        window.addEventListener('scroll', this.parallax)
    }

    render() {
        const {link, text1pos, text2pos} = this.state
        const title = "Gift Guide"
        console.log(title.split(''))

        return (
            <div className={`col-25 heading ${this.props.bgcolor}`}>
                <div
                    className="offset-7 col-11 col-s-25 mgt-3 portrait video-inner"
                    dangerouslySetInnerHTML={{
                    __html: `<video class="video portrait" autoplay loop muted playsinline> <source src=${this.props.video} alt="video" type="video/mp4"/></video>`
                }}/>
                <div
                    className="title-double mgt-4 offset-1 col-23 justify-center absolute">
                    <p ref={this.elt1} className="very-big-title light" style={{
                    transform: `translateY(${text1pos}px)`
                }}>Gift
                    </p>
                    <p className="space"></p>
                    <p ref={this.elt2} className="very-big-title light" style={{
                    transform: `translateY(${text2pos}px)`
                }}>Guide</p>
                </div>
            </div>
        )
    }
}

export default Header2