import React from 'react';

class ProductTile extends React.Component {
    constructor() {
        super()
        this.elt = React.createRef();
        this.reveal = this
            .reveal
            .bind(this)
        this.state = {
            scale: 'scale-10'
        }
        this.setScale = this.setScale.bind(this)
        this.setScale2 = this.setScale2.bind(this)
    }

    reveal() {
        const node = this.elt.current;
        const dist = node.getBoundingClientRect().top;
        if (dist < window.innerHeight) {
            setTimeout(() => node.classList.remove('hide'), 500)
        } else {
            setTimeout(() => node.classList.add('hide'), 500)
        }
    }

    setScale() {
        this.setState({scale: 'scale-11'})
    }
    setScale2() {
        this.setState({scale: 'scale-10'})
    }

    componentDidMount() {
        window.addEventListener('scroll', this.reveal)
    }

    render() {
        const {img, alt} = this.props;

        return (
            <div
                ref={this.elt}
                onMouseOver={this.setScale}
                onMouseLeave={this.setScale2}
                className={`${this.state.scale} offset-1 col-7 col-s-11 mgbt-1 mgbt-m-2 flex-col portrait mid`}>
                <div className="row portrait">
                    <img className="tileimg" src={img} alt={alt}/>
                </div>
                <a className="text-s dark mgt-text-10" href="#">See product</a>
            </div>
        )
    }
}

export default ProductTile