import React from 'react';

class Tile extends React.Component {
    constructor() {
        super()
        this.wrapper = React.createRef();

        this.state = {
            numberOf: 'col-7',
            alignment: 'offset-1',
        }

        this.reveal = this
            .reveal
            .bind(this)

        this.setNumber = this
            .setNumber
            .bind(this)

        this.setAlignment = this
            .setAlignment
            .bind(this)

    }

    setNumber() {
        if (this.props.numberOf === 'two') {
            this.setState({numberOf: 'col-11'})
        }
        if (this.props.numberOf === 'three') {
            this.setState({numberOf: 'col-7'})
        }
    }

    setAlignment() {
        if (this.props.alignment === 'start') {
            this.setState({alignment: 'offset-1'})
        }
        if (this.props.alignment === 'center') {
            this.setState({alignment: 'offset-7'})
        }
    }

    reveal() {
        const node = this.wrapper.current;
        const rect = node.getBoundingClientRect();
        const dist = rect.top
        if (dist < window.innerHeight) {
            setTimeout(() => node.classList.remove('hide'), 500)
            setTimeout(() => node.classList.add('show'), 500)

        } else {
            setTimeout(() => node.classList.add('hide'), 500)
            setTimeout(() => node.classList.remove('show'), 500)
        }
    }

    componentDidMount() {
        window.addEventListener('scroll', this.reveal)
        window.addEventListener('load', this.setNumber)
        window.addEventListener('load', this.setAlignment)

    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.reveal)
        window.removeEventListener('scroll', this.setNumber)
        window.removeEventListener('scroll', this.setAlignment)

    }

    render() {
        const {img, alt, title, cta} = this.props

        return (
            <div
                ref={this.wrapper}
                className={`${this.state.alignment} ${this.state.numberOf} offset-s-1 col-s-23 mgbt-1 mgbt-m-2 flex-col full hide`}>
                <div className="row portrait">
                    <img className="tileimg" src={img} alt={alt}/>
                    <div className="inner-bottom offset-1 mgbt-1 mgbt-m-2">
                        <h3 className="inner-title light mgbt-text-10">{title}</h3>
                        <a className="text-s light" href="#">{cta}</a>
                    </div>
                </div>
            </div>
        )
    }
}

export default Tile
