import React from 'react';

class Navigation extends React.Component {
    constructor() {
        super()

        this.wrapper = React.createRef();
        this.state = {
            height: '',
            position: 0,
            links: {
                men: 'For men',
                women: 'For women',
                kids: 'For kids'
            },
            bgcolor: 'transparent',
            color: 'light'

        }

        this.fixed = this
            .fixed
            .bind(this)

            this.fadeIn = this
            .fadeIn
            .bind(this)

    }

    fixed() {

        const limit = window.scrollY + 80;
        if (limit > window.innerHeight) {
            this.setState({height: 'navtop', bgcolor: 'white', color: 'dark'})
        } else {
            this.setState({height: '', bgcolor: 'transparent', color: 'light'})
        }
    }

    fadeIn() {
        const node = this.wrapper.current;
        setTimeout(() => node.classList.remove('hide'), 800)
    }

    componentDidMount() {
        window.addEventListener('scroll', this.fixed)

    }

    componentWillUnmount() {
        window.addEventListener('scroll', this.fixed)
    }

    render() {
        const {height, links, bgcolor, color} = this.state

        return (
            <div ref={this.wrapper} className={`row bg ${bgcolor} ${height}`}>
                <div
                    className="offset-8 col-9 offset-s-1 col-s-21 align-center justify-between">
                    <a
                        className={`navigation-link ${color} ${this.props.hide1}`}
                        onClick={this.props.eventmen}
                        href="#">{links.men}</a>
                    <a
                        className={`navigation-link ${color} ${this.props.hide2}`}
                        onClick={this.props.eventwomen}
                        href="#">{links.women}</a>
                    <a
                        className={`navigation-link ${color} ${this.props.hide3}`}
                        onClick={this.props.eventkids}
                        href="#">{links.kids}</a>
                </div>
            </div>
        )
    }
}

export default Navigation