import React from 'react'
import leather from './images/leather.jpg'
import fragrances from './images/fragrances.jpg'
import watch from './images/watch.jpg'

class Supercomponent extends React.Component {
    constructor() {
        super()
        this.elt1 = React.createRef();
        this.elt2 = React.createRef();
        this.elt3 = React.createRef();

        this.state = {
            category: '',
            img: watch
        }

        this.changeCat = this
            .changeCat
            .bind(this)
    }

    changeCat() {
        const pos1 = this
            .elt1
            .current
            .getBoundingClientRect()
            .top;
        console.log(pos1)

        if (pos1 < 953) {
            this.setState({category: 'Watches', img: watch})
        } if (pos1 < 0){
            this.setState({category: 'Leather Goods', img: leather})
        }  if (pos1 < -900){
            this.setState({category: 'Fragrances', img: fragrances})
        }
    }

    componentDidMount() {
        window.addEventListener('scroll', this.changeCat)
    }

    render() {
        const {category, img} = this.state
        return (
            <div className="col-25 mgbt-2 flex-col relative">
                <div className="background">
                    
                    <div className="offset-3 col-12 mgbt-1 fullheight sticky">
                        <img className="tileimg " src={img} alt="image"/>
                        <h2 className=" big-title text-custom dark mgbt-text-10">{category}</h2>
                    </div>
                    <div ref={this.elt1} className="rari"></div>

                </div>

            </div>
        )
    }
}

export default Supercomponent